<template><div>

<div :class="$css.title"> Сравнение Nao с Эсперанто </div>

<ol>

<li>
Эсперанто обладает богатыми возможностями словообразования. Однако, в нём  имеется бесчисленное количество корней, которые могли бы быть выражены через другие корни, без какой-либо потери смысла. Например:
<div class="pa-5">
	<div>studento – lernulo</div>
	<div>hospitalo – kuracejo</div>
	<div>kuraci – sanigi</div>
	<div>murdi − mortigi</div>
	<div>oftalmologo – okulisto</div>
</div>

И таких примеров − тысячи. Словари Эсперанто насчитывают десятки тысяч корней. Запомнить их всех представляется огромной преградой для овладевания. Nao стремится по максимуму избегать лишних корней, т.е. тех, которые можно соорудить из уже имеющихся. Стоит однако заметить, что во многих случаях слова могут получаться слишком длинными и трудными к произношению, т.к. сами корни в эсперанто в среднем довольно длинные. В Nao каждый корень состоит из одного слога, что позволяет строить сочетания из большого количества морфем.

<p>Зачастую Эсперанто совсем не использует словообразование там, где бы оно очень упростило запоминание. Например, названия месяцев и дней недели можно сделать из чисел, как это работает в китайском и вьетнамском. В Nao сделано именно так.</p>

<p>В Эсперанто часто можно увидеть ряд корней, которые очень близки по смыслу. Например, корни <b>komik-, rid-, komedi-, ŝerc-</b> все описывают очень тесно связанные понятия, но являются разными корнями. В Nao все эти понятия логически строятся из базового слова <span :class="$css.naoword">hal</span> = «смеяться».

<div class="pa-5">
	<div>ridi = <span :class="$css.naoword">hal</span></div>
	<div>komika = <span :class="$css.naoword">dohal</span> (<i>такой, над которым смеются</i>)</div>
	<div>komedio = <span :class="$css.naoword">diohal</span> (<i>смешная история</i>)</div>
	<div>ŝerci = <span :class="$css.naoword">tuanehal</span> (<i>стремиться рассмешить</i>)</div>
</div>

 Ещё пример: корни <b>tim-, kuraĝ-, danĝer-, sav-, sekur-, protekt-, defend-, gard-, risk-, minac-, rifuĝ-</b> в Nao можно построить из базового слова <span :class="$css.naoword">bun</span> = «быть в опасности».</p>

<div class="pa-5 dlist">
	<div>timi → <span :class="$css.naoword">nabun</span> <br>(<i>испытывать чувство/эмоцию, сигнализирующую об опасности</i>)</div>
	<div>kuraĝa → <span :class="$css.naoword">peunabun</span> <br>(<i>склонный не испытывать чувство/эмоцию, сигнализирующую об опасности</i>)</div>
	<div>savi → <span :class="$css.naoword">nenogabun</span> <br>(<i>сделать так, чтобы <объект> не был в полной опасности</i>)</div>
	<div>sekura → <span :class="$css.naoword">dobin</span> <br>(<i>такой, от которого не находятся в опасности</i>)</div>
	<div>protekti/defendi → <span :class="$css.naoword">nebin</span> <br>(<i>делать так, чтобы <объект> был в безопасности</i>)</div>
	<div>riski → <span :class="$css.naoword">fasebun</span> <br>(<i>совершать опасные для себя действия</i>)</div>
	<div>singarda → <span :class="$css.naoword">tuabin</span> <br>(<i>стремиться быть в безопасности</i>)</div>
	<div>rifuĝejo → <span :class="$css.naoword">mabin</span> <br>(<i>специальное место для того, чтобы быть в безопасности</i>)</div>
</div>


<p>Таким образом, количество базовых слов в Nao гораздо меньше, но при этом выразительная способность ничуть не уступает Эсперанто.</p>
</li>
<li> В Эсперанто мужской пол субъекта часто «вшит» в слово по умолчанию, а меняется на женский при помощи суффикса -in-. Это неудобно и совершенно справедливо вызывает много критики. В Nao никакое слово по умолчанию не содержит в себе информацию о поле, но уточняется отдельно при помощи слов <span :class="$css.naoword">mi</span> = «особь женского пола», <span :class="$css.naoword">mu</span> = «особь мужского пола». К примеру:

<div class="pa-5">
	<span :class="$css.naoword">pau</span> = родитель<br>
	<span :class="$css.naoword">mipau</span> = мать<br>
	<span :class="$css.naoword">mupau</span> = отец<br>
</div>
<div class="pa-5">
	<span :class="$css.naoword">paikio</span> = племянник/племянница<br>
	<span :class="$css.naoword">mipaikio</span> = племянница<br>
	<span :class="$css.naoword">mupaikio</span> = племянник<br>
</div>

<p>В том числе имеется и слово <span :class="$css.naoword">data</span>, дословно означающее «(э)тот человек», являющееся нейтральным эквивалентом «он/она».</p>
</li>
<li>
<p>Зачастую корни в Эсперанто имеют расплывчатое значение, либо много значений. Ситуация ухудшается тем, что люди, будучи из разных культур, начинают вкладывать свои собственные нюансы в слова. К примеру, в одних словарях написано, что studento это «студент», а в других - просто «учащийся». Кому из них верить - непонятно. В Nao такой проблемы нет, т.к. количество базовых корней мало и у каждого ровно одно строгое значение. </p>
</li>
<li>
<p>Эсперанто использует 23 согласных, в то время как Nao оперирует всего 14-ю. Также, сочетаний согласных в Nao тоже гораздо меньше. Это делает его значительно проще в произношении, особенно для неевропейцев. Также, в Nao отсутствуют буквы с диакритикой, что упрощает ввод текста и меньше отпугивает начинающих.</p>
</li>
<li>
В грамматике Эсперанто имеются некоторые неприятные элементы.

<ol type="a">
<li> Противоположность образуется префиксом mal-; это длинно и вызывает много недовольства. В Nao, противоположность образуется чередованием i/u, например <span :class="$css.naoword">li</span> = <b>bona</b>, <span :class="$css.naoword">lu</span> = <b>malbona</b> </li>
<li> Определённый артикль <b>la</b>, которому абсолютно не место в международном языке. В Nao артиклей нет. </li>
<li> Окончание аккузатива <b>-n</b> доставляет многим людям головную боль. В Nao есть показатель аккузатива, но он является опциональным (используется только при нестандартном порядке слов). </li>
<li> Вездесущий <b>estas</b>, который мы видим почти в каждом предложении, начинает действовать на нервы.</li>
<li> Обязательные показатели времени <b>-is,-as,-os</b>, которые многие путают из-за чрезмерной схожести. В Nao время глагола указывать не обязательно, т.к. оно в большинстве случаев понятно из контекста. </li>
</ol>
</li>
<li>
	В Эсперанто отсутствуют официальные правила транслитерации слов с других языков. В результате мы наблюдаем хаос, когда одно и то же имя собственное может выглядеть в Эсперанто по-разному. К примеру:

	<div class="pa-5">
		<div> <b>Houston</b> → Hjustono / Hustono / Houstono</div>
		<div> <b>Евгения</b> → Jevgenija, Evgenija, Evgenia, Eŭgenija,...</div>
		<div> <b>香港</b> → Honkongo / Hongkongo</div>
		<div> <b>西安</b> → Ŝjiano / Siano</div>
	</div>

	Зачастую имена не транслитерируются совсем, и совершенно непонятно, как их произносить. Прикрепление к заимствованиям окончания <b>-o</b>, чего требует грамматика эсперанто, также выглядит довольно нелепо. В Nao для каждого языка прописаны <router-link to="/transliterations">официальные правила транслитерации</router-link>. Таким образом, у каждого слова имеется только один правильный способ написания.
</li>



</ol>



</div></template>

