<template><div class="home">

<div :class="lesson_title"> Фразы </div>

<div> Вот некоторые фразы и предложения, которые могут дать общее представление о языке и о том, как он работает. </div>

<PAT :pairs="phrases"></PAT>


</div></template>

<script>

import PAT from '@/components/PhrasesAndTranslations'

export default {
	components: {PAT},
	data() {
		return {
			lesson_title: 'text-h3 text-center ma-5',
		}
	},
	methods: {

	},
	created() {

	},
	computed: {
		phrases() {
			let res = {};
			//console.log('computing phrases');
`joimit				Привет!
me nom 'petia		Меня зовут Петя.
te nom ke			Как тебя зовут?
te najan ke			Как настроение?
li					Хорошо.
lu					Плохо.
joikohunfali		Спасибо.
nalufalu			Извините.
no tal				Не за что / Ничего страшного
dei					Да
deu					Нет
me hom				Я понимаю.
ta dog me			Это моя собака.
ta ke				Что это?
ca ta miau te		Это твоя кошка?
ca dom 'petia cun	Дом Пети высокий?
'piter sit			Питер сидит.
te no sit			Ты не сидишь.
me fi miau			Я люблю кошек.
te fi ke			Что ты любишь?
ca miau fi son		Кошки любят спать?
ca 'petia fu dog		Петя не любит собак?
'jeni go son		Дженни идёт спать.
'piter moi sit		Питер хочет сесть.
nui 'tom moi sit	Том тоже хочет сесть.
me ku go 'madrid	Я поеду в Мадрид.
me ki go 'istanbul	Я ездил в Стамбул.
he go				Иди!
he no bai ta		Не покупай это!
muta jo fos 'banan	У него есть четыре банана.
'alis pa 'london	Алис в Лондоне`
			.split('\n').map( function(line) {
				let s = line.split(/\t+/);
				res[s[0]] = s[1];
			});
			return res;
		}
	},
}
</script>
